@import '../../styles/variables.css';

.container {
    position: absolute;
    align-items: center;
    background-color: var(--Gray-5);
    display: flex;
    flex-direction: column;
    gap: 35px;
    height: -webkit-fill-available;
    padding: 20px 20px 30px;
    width: -webkit-fill-available;
}

.title {
    align-items: center;
    color: var(--brand-color);
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    text-align: center;
    margin: 0;
}

.message {
    align-items: center;
    display: flex;
    flex-direction: column;
    color: var(--gray-text-color);
    font-family: Verdana;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.56px;
    flex-wrap: wrap;
    text-align: center;
}

.strong {
    color: black;
}

.button {
    font-size: 12px;
    font-weight: 400;
}

.infoCont {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;

}

.infoText {
    align-items: center;
    color: var(--gray-text-color);
    display: flex;
    flex-wrap: wrap;
    font-family: Verdana;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    justify-content: center;
    line-height: normal;
    /* letter-spacing: 0.56px; */
    text-align: center;
    padding: 0;
    margin: 0;
}

.link {
    color: blue;
    font-family: Verdana;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.56px;
    text-decoration: none;
    transition: color 0.3s ease, transform 0.3s ease;
    /* Add transition for color and scaling */
}

.link:hover,
.link:focus {
    color: var(--brand-color);
    transform: scale(1.1);
    /* Use 'transform' for scaling */
}

@media screen and (width >=768px) {
    .container {
        gap: 50px;
    }

    .infoCont {
        gap: 20px;
    }

}